.banner {
    margin-top: -10vh;
    height: 100vh;             
    color: white;
    object-fit: contain;
    display: flex;              
    align-items: center;        
    position: relative;         
    margin-bottom: 0;           
    padding-left: 30px;         
}

.banner_contents {
    max-width: 500px;           
    /*  padding-top: 300px; */
}

.banner_title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner_description {
    width: 45rem;
    line-height: 1;
    padding: 1rem 0;
    font-size: 1rem;
    max-width: 360px;
    height: 80px;
}

.banner_button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-size: 1.2rem;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 1rem 3rem;
    margin-right: 2rem;
    background-color: rgba(51, 51, 51, 0.5);
}

.play {
    background-color: #e6e6e6;
    color: #000;
}

.banner_button:hover {
    color: #000;
    background-color: #c6c6c6;
    transition: all 0.2s;
}

.banner_fadeBottom {
    position: absolute;        
    bottom: 0;
    left: 0;
    width: 100%;
    height: 20vh;             
    background-image: linear-gradient(
        180deg,
        transparent,
        rgba(37, 37, 37, 0.41),
        #111
    );
    
}
